import $ from '../core';

$.prototype.accordion = function(headActive = 'accordion-head--active', contentActive = 'accordion-content--active', paddings = 40) {
    for (let i = 0; i < this.length; i++) {
        $(this[i]).click(() => {
            let childClosed = this[i].querySelector('.toggle-icon-closed');
            let childOpened = this[i].querySelector('.toggle-icon-opened');

            $(childClosed).toggleClass('fadeOut');
            $(childClosed).toggleClass('fadeIn');

            $(childOpened).toggleClass('fadeOut');
            $(childOpened).toggleClass('fadeIn');

            $(this[i]).toggleClass(headActive);
            $(this[i].nextElementSibling).toggleClass(contentActive);

            if (this[i].classList.contains(headActive)) {
                this[i].nextElementSibling.style.maxHeight = this[i].nextElementSibling.scrollHeight + paddings + "px";
            } else {
                this[i].nextElementSibling.style.maxHeight = "0px";
            }
        });
    }
};



$('.accordion-head').accordion();